* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nav-list {
  height: 10vh;
  width: 50%;
  margin-left: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.nav-link {
  text-decoration: none;
  color: #000;
  font-size: 1.2rem;
}

.home,
.about {
  height: 90vh;
  width: 100%;
  position: absolute;
}
.home {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
.about {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
.home-content,
.about-content {
  display: flex;
  height: 100%;
  justify-content: space-evenly;
  align-items: center;
  img {
    height: 70%;
  }
  h1 {
    font-size: 50px;
    margin: 30px 0;
  }
  p {
    font-size: 20px;
  }
}
